.video-snippet {
	background: #f6ca30;
	position: relative;
	margin-top: 120px;
	padding-top: 40px;
	padding-bottom: 80px;
	z-index: 0;
	.section-divider {
		position: absolute;
		top: -80px;
		z-index: 1;
		left: 0;
		right: 0;
		line-height: 0; /* remove stray gaps */
		--divider-color: #f6ca30; /* default color */
		color: var(--divider-color);
	}

	/* Make SVGs fluid */
	.section-divider__svg {
		display: block;
		width: 100%;
		height: auto;
	}

	/* Show mobile by default, swap at breakpoint */
	.section-divider__svg--desktop {
		display: none;
	}
	.section-divider__svg--mobile {
		display: block;
	}

	@media (min-width: 768px) {
		.section-divider__svg--desktop {
			display: block;
		}
		.section-divider__svg--mobile {
			display: none;
		}
	}
	.video-snippet__header {
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		padding-bottom: 40px;
		padding-bottom: 80px;
		.video-snippet__chip {
			padding: 8px 32px;

			/* Neutral/White1 */
			background: #ffffff;
			border-radius: 50px;

			/* Inside auto layout */
			flex: none;
			order: 0;
			flex-grow: 0;

			/* Desktop_B2 */
			font-family: "DM Sans";
			font-style: normal;
			font-weight: 600;
			font-size: 14px;
			line-height: 16px;
			/* identical to box height, or 114% */

			/* Neutral/Black2 */
			color: #161a1d;
			margin-bottom: 16px;
		}
		.video-snippet__title {
			font-family: "Neilvard One";
			font-style: normal;
			font-weight: 400;
			font-size: 24px;
			line-height: 24px;
			text-align: center;

			/* Neutral/Black2 */
			color: #161a1d;
		}
	}
	.video-snippet__videos {
		display: grid;
		gap: 20px;
		grid-template-columns: repeat(4, 1fr);
		video {
			width: 100%;
			height: 100%;
			aspect-ratio: 9/16;
		}
	}
}


/* Tablet & mobile: make it horizontal scroll */
@media (max-width: 1024px) {
	.video-snippet__videos {
		display: flex;
		overflow-x: auto;
		gap: 16px;
		scroll-snap-type: x mandatory;
		-webkit-overflow-scrolling: touch;
		padding: 0 10px;
	}

	.video-snippet__videos video {
		flex: 0 0 auto;
		width: 250px; /* adjust to your design */
		scroll-snap-align: start;
	}
}